@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
html {
  box-sizing: border-box;
}

body {
  margin: 0;
  background: #ecf0f1;
  font-family: 'Roboto';
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.app-title {
  color: #353b48;
  font-size: 48px;
}

.card-container {
  display: flex;
  grid-gap: 30px;
  gap: 30px;
}

@media screen and (max-width: 800px) {
  .app-title {
    font-size: 36px;
  }

  .card-container {
    flex-direction: column;
  }
}

.leaderboard-row {
  display: flex;
  justify-content: space-between;
  padding: 30px;
}

.even {
  background: rgb(0 0 0 / 10%);
}

.odd {
  background: rgb(0 0 0 / 22%);
}

@media screen and (max-width: 600px) {
  .leaderboard-row {
    font-size: 14px;
  }
}

.info-card {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  background: white;
  padding: 40px 140px 40px 30px;
  border-radius: 20px;
  box-shadow: 0 6px 20px -1px rgb(0 0 0 / 20%);
  cursor: pointer;
  transition: ease 0.2s;
}

.info-card:hover {
  transform: scale(1.05);
}

.info-card.Deaths:hover,
.info-card.Cases:hover {
  border-top: 8px solid #e74c3c;
}

.info-card.Recovered:hover {
  border-top: 8px solid #2ecc71;
}

.info-card .card-title,
.all-time-infos {
  color: darkgray;
}

.today-infos {
  font-weight: 700;
  font-size: 28px;
}

.today-infos.Deaths,
.today-infos.Cases {
  color: #e74c3c;
}

.today-infos.Recovered {
  color: #2ecc71;
}

@media screen and (max-width: 1000px) {
  .info-card {
    padding: 20px 100px 20px 30px;
  }
}

.scroll {
  width: 70%;
  border-radius: 6px;
  box-shadow: 0 6px 12px -1px rgb(0 0 0 / 40%);
  margin-bottom: 30px;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #333;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #666;
}

.select-container {
  margin-bottom: 30px;
}

.select {
  font-family: 'Roboto';
  font-size: 20px;
  padding: 15px;
  outline: none;
  border: none;
  border-radius: 12px;
  background: white;
  width: 200px;
  cursor: pointer;
  box-shadow: 0 6px 20px -1px rgb(0 0 0 / 40%);
}

